import Vue from 'vue'
import VueRouter from 'vue-router'
import MainPage from '@/views/MainPage.vue'
import CardPage from '@/views/CardPage.vue'


Vue.use(VueRouter)

const routes = [ 
  {
   path: '/',
   component: MainPage,
  
  },
  {
    path:'/card',
    component: CardPage,
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
